import { ReactNode } from 'react';export default function TierCard({name,price,subtitle,features,cta}:{name:string;price:string;subtitle?:string;features:string[];cta:ReactNode;}){return(<div className='glass p-6 flex flex-col'><div><h3 className='text-xl font-semibold'>{name}</h3>{subtitle&&<p className='mt-1 text-sm text-white/70'>{subtitle}</p>}<p className='mt-4 text-3xl font-bold'>{price}</p><ul className='mt-6 space-y-2 text-sm text-white/70 list-disc pl-5'>{features.map((f,i)=>(<li key={i}>{f}</li>))}</ul></div><div className='mt-6'>{cta}</div></div>);}